

























import {
	mapState
} from 'vuex'

export default {
	data() {
		return {
			fileid: null,
			fieldArray: []
		}
	},
	computed: mapState(['accessToken']),
	onLoad: function(options) {
		this.fileid = options.id;
		uni.showLoading({
			title: '加载中'
		});
		uni.request({
			url: this.GLOBAL + '/rest/basFileController/getContent/' + this.fileid,
			method: 'GET',
			data: {},
			header: {
				'content-type': 'application/json',
				'X-Auth-Token': this.accessToken
			},
			success: (res) => {
				uni.hideLoading();
				if (res.statusCode === 200) {
					this.fieldArray = res.data.data;
					console.log(this.fieldArray);
				}
			}
		});
	},
	methods: {
		formSubmit: function(e) {
			console.log(JSON.stringify(e.detail.value));
			uni.showLoading({
				title: '提交中'
			});
			uni.request({
				url: this.GLOBAL + '/rest/basFileController/postFileUrl',
				method: 'POST',
				data: {
					detailvalue:e.detail.value,
					id:this.fileid
				},
				header: {
					'content-type': 'application/json',
					'X-Auth-Token': this.accessToken
				},
				success: (res) => {
					uni.hideLoading();
					if (res.statusCode === 200) {
						let vardata = res.data.data;
						uni.downloadFile({
							url: this.GLOBAL + '/'+vardata,
							success: (res) => {
								uni.hideLoading();
								wx.openDocument({
								  filePath: res.tempFilePath ,
								  success: function (res) {
									uni.showToast({
										icon: 'none',
										title: "打开文档成功",
									});
								  }
								});
							},
							fail: (err) => {
								uni.showToast({
									icon: 'none',
									title: "错误消息:"+err,
								});
							}
						})
						console.log(vardata);
					}
				}
			});
		},
		formReset: function(e) {
			console.log("清空数据")
			this.chosen = ''
		}

	}
}
